.title {
    color: $color-titles;
    @include res-font(30, 25);
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    &--left {
        color: $color-white;
        text-align: left;
        @include res-font(36, 22); }
    &-header {
        color: $color-titles;
        @include res-font(18, 18);
        font-weight: bold;
        &--center {
            text-align: center;
            margin-top: 38px; } }
    &-footer {
        color: $color-titles;
        @include res-font(14, 14);
        font-weight: regular;
        &--center {
            text-align: center;
            margin-top: 25px;
            span {
                color: $main-color;
                text-decoration: underline; } } } }

@media (max-width: 767px) {
    .title {
        &-header {
            &--center {
                margin-top: 20px; } }
        &--left {
            text-align: center; } } }
