.carousel {
    padding: 81px 15px;
    &__wrapper {
        display: flex;
        justify-content: center;
        position: relative;
        max-width: 100%;
        height: auto; }
    &__inner {
        width: 100%;
        max-width: 750px; }
    &__item {
        outline: none;
        img {
            max-width: 100%;
            height: auto; } }
    .slick {
        &-prev {
            position: absolute;
            top: calc(50% - 25px);
            left: -96px;
            z-index: 1;
            background-color: transparent;
            border: none;
            &:focus {
                outline: none; } }
        &-next {
            position: absolute;
            top: calc(50% - 25px);
            right: -96px;
            z-index: 1;
            background-color: transparent;
            border: none;
            &:focus {
                outline: none; } }
        &-dots {
                display: flex;
                justify-content: space-between;
                width: 70px;
                list-style: none;
                position: absolute;
                bottom: -60px;
                left: calc(50% - 35px);
                li {
                    button {
                        display: block;
                        background-color: $main-color;
                        border-radius: 50%;
                        width: 15px;
                        height: 15px;
                        font-size: 0;
                        border: none;
                        &:focus {
                            outline: none; } }
                    &.slick {
                        &-active {
                            button {
                                display: block;
                                background-color: transparent;
                                border-radius: 50%;
                                width: 15px;
                                height: 15px;
                                font-size: 0;
                                border: 3px solid $main-color;
                                &:focus {
                                    outline: none; } } } } } } } }

@media (max-width: 991px) {
    .carousel {
        &__wrapper {
            max-width: 500px;
            margin: 0 auto; } }
    .slick {
        &-prev {
            img {
                display: none; } }
        &-next {
            img {
                display: none; } } } }

