.footer {
    height: 630px;
    position: relative;
    &__info {
        position: absolute;
        top: 0;
        left: calc(50% - 365px);
        width: 730px;
        max-width: 100%;
        background-color: $main-color;
        text-align: center;
        padding: 50px 0 60px; }
    &__addr {
        color: $color-white;
        @include res-font(20, 18);
        font-weight: 500; }
    &__phone {
        display: block;
        color: $color-white;
        font-size: 20px;
        font-weight: 700;
        margin: 36px 0 38px;
        &:hover {
            color: $color-white; } } }

.footer {
    &-social {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 210px;
        max-width: 100%;
        margin: 0 auto;
        &__link {
            width: 50px;
            height: 50px; }
        &__link:nth-child(1) {
            background: url('../images/icons/facebook-icon.svg') no-repeat;
            transition: .3s all;
            &:hover {
                opacity: .5; } }
        &__link:nth-child(2) {
            background: url('../images/icons/instagram-icon.svg') no-repeat;
            transition: .3s all;
            &:hover {
                opacity: .5; } }
        &__link:nth-child(3) {
            background: url('../images/icons/pinterest-icon.svg') no-repeat;
            transition: .3s all;
            &:hover {
                opacity: .5; } } } }

@media (max-width: 768px) {
    .footer {
        &__info {
            width: 540px;
            left: calc(50% - 270px); } } }

@media (max-width: 565px) {
    .footer {
        &__info {
            width: 100%;
            left: 0;
            padding: 50px 15px 60px; } } }
