.catalog {
    padding: 108px 0;
    &__tabs {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 70px;
        margin-bottom: 39px;
        list-style: none; }
    &__tab {
        width: 220px;
        height: 50px;
        border: 2px solid #d0d0d0;
        color: #6b6b6b;
        @include res-font(18, 16);
        line-height: 48px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        transform: skew(-25deg);
        margin: 0 5px;
        transition: .3s all;
        div {
            transform: skew(25deg); }
        &:hover {
            background-color: #eeebeb; }
        &--active {
            border: none;
            background-color: $main-color;
            &:hover {
                background-color: $main-color; }
            div {
                color: $color-white;
                transform: skew(25deg);
                &:hover {
                    color: $color-white; } } } }
    &__link {
        display: block;
        color: #6b6b6b;
        transform: skew(25deg);
        &:hover {
            color: #6b6b6b;
            text-decoration: none; } }

    &__content {
        display: none;
        justify-content: flex-start;
        flex-wrap: wrap;
        width: 980px;
        &--active {
            display: flex;
            max-width: 962px; } } }
.catalog {
    &-item {
        width: 300px;
        max-width: 100%;
        margin: 20px 10px 0;
        padding: 20px 20px 26px;
        border: 2px solid #e8e8e8;
        overflow: hidden;
        &__inner {
            display: flex;
            width: calc(200% + 40px);
            transform: translateX(0);
            transition: .5s all;
            &--hidden {
                transform: translateX(calc(-50% - 20px)); } }
        hr {
            width: 260px;
            max-width: 100%;
            margin: 19px auto 21px;
            border: 1px solid #e8e8e8; }
        &__product {
            width: 260px;
            max-width: 100%;
            text-align: center;
            position: relative; }
        &__img {
            display: block;
            width: 140px;
            max-width: 100%;
            max-height: 200px;
            margin: 0 auto; }
        &__title {
            color: $color-titles;
            @include res-font(16, 16);
            font-weight: 700;
            margin-top: 20px; }
        &__desc {
            color: $color-titles;
            @include res-font(14, 14);
            font-weight: 300;
            margin: 16px 0 0;
            padding-bottom: 34px; }
        &__link {
            position: absolute;
            display: block;
            bottom: 0;
            left: calc(50% - 45px);
            color: $main-color;
            @include res-font(14, 14);
            font-weight: 500;
            text-transform: uppercase;
            text-decoration: underline; }
        &__item {
            width: 260px;
            max-width: 100%;
            margin-left: 42px;
            padding-left: 18px;
            position: relative;
            li {
                position: relative;
                color: #555555;
                @include res-font(14, 14);
                font-weight: 300;
                &::before {
                    content: '';
                    position: absolute;
                    top: 7px;
                    left: -19px;
                    width: 5px;
                    height: 5px;
                    background-color: $main-color; } } }
        &__list {
            list-style: none; } } }

.item {
    &__link {
        position: absolute;
        display: block;
        bottom: 0;
        left: calc(50% - 27px);
        color: $main-color;
        @include res-font(14, 14);
        font-weight: 500;
        text-transform: uppercase;
        text-decoration: underline; } }

.price {
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: {}
        width: 260px;
        max-width: 100%; }
    &__old {
        color: #555555;
        @include res-font(14, 14);
        font-weight: 300;
        text-decoration: line-through; }
    &__new {
        color: #050505;
        @include res-font(20, 18);
        font-weight: 700; } }

@media (max-width: 991px) {
    .catalog {
        padding: 50px 0;
        &__title {
            margin: 0 0 20px; }
        &__tabs {
            display: none; }
        &__content {
            margin-top: 30px;
            &--active {
                width: 720px;
                justify-content: center;
                margin-top: 70px; } } } }

@media (max-width: 768px) {
    .catalog {
        &__content {
            &--active {
                width: 540px;
                justify-content: center;
                margin: 70px auto 0; } } } }

@media (max-width: 575px) {
    .catalog {
        padding-top: 30px;
        &__content {
            margin-top: 10px;
            &--active {
                width: 100%;
                margin: 70px auto 0; } } } }
