.select {
    position: absolute;
    left: calc(50% - 125px);
    cursor: pointer;
    font-size: 16px;
    width: 250px;
    max-width: 100%;
    margin: 0 auto;
    z-index: 1;
    &__input {
        width: 250px;
        max-width: 100%;
        padding: 10px 15px;
        display: flex;
        justify-content: space-between;
        border-radius: 5px;
        align-items: center;
        background-color: $main-color;
        color: $color-white; }
    &__dropdown {
        display: none;
        width: 250px;
        max-width: 100%;
        margin-top: 2px;
        border-radius: 0 0 5px 5px;
        background-color: $main-color;
        color: $color-white;
        padding: 0 15px;
        &--active {
            display: block; } }
    &__list {
        list-style: none; }
    &__item {
        padding: 10px 0;
        transition: .5s all;
        &:hover {
            color: yellow; } } }

.select {
    &__item.selected {
        color: yellow;
        font-weight: 700; } }

@media (min-width: 991px) {
    .select {
        display: none; } }
