.feed {
    &-form {
        display: flex;
        flex-direction: column;
        width: 300px;
        input {
            width: 100%;
            height: 50px;
            border-radius: 5px;
            background-color: $color-white;
            margin-bottom: 15px;
            padding: 0 20px;
            border: none;
            outline: none;
            color: $color-form-text;
            @include res-font(14, 14);
            font-weight: 300; }
        &--mt {
            margin-top: 25px; } } }

@media (max-width: 575px) {
    .feed {
        &-form {
            &--mt {
                width: 250px; } } } }
