.reviews {
    padding: 5px 0 71px;
    &__title {
        margin-bottom: 70px; }
    &__wrapper {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        width: 100%; }
    &__review {
        display: flex;
        justify-content: space-between;
        width: 881px;
        max-width: 100%;
        margin-bottom: 100px;
        img {
            width: 169px;
            height: 169px; }
        &:last-child {
            margin-bottom: 0; } } }

.reviews {
    &-text {
        width: 650px;
        max-width: 100%;
        &__title {
            color: $color-titles;
            font-size: 18px;
            font-weight: 700; }
        &__subtitle {
            color: #646464;
            font-size: 14px;
            font-weight: 300;
            margin: 10px 0 19px; }
        &__text {
            color: $color-titles;
            font-size: 16px;
            font-weight: 300; } } }

@media (max-width: 991px) {
    .reviews {
        &-text {
            width: 450px; } } }

@media (max-width: 767px) {
    .reviews {
        &__title {
            margin-top: 30px;
            margin-bottom: 50px; }
        &__review {
            flex-direction: column;
            align-items: center;
            margin-bottom: 50px; } }

    .reviews {
        &-text {
            text-align: center;
            &__title {
                margin-top: 20px; } } } }
