.button {
    width: 150px;
    height: 30px;
    border-radius: 5px;
    background-color: $main-color;
    border: none;
    outline: none;
    cursor: pointer;
    color: #ffffff;
    @include res-font(12, 10);
    font-weight: 500;
    text-transform: uppercase;
    transition: .3s all;
    &:hover {
        background-color: #e90202;
        color: #f0ebc4; }
    &:focus {
        border: none;
        outline: none; }
    &--main {
        width: 300px;
        height: 50px;
        color: #ffffff;
        font-size: 16px;
        font-weight: 500;
        text-transform: uppercase; }
    &--submit {
        width: 100%;
        height: 55px;
        font-size: 16px;
        font-weight: medium;
        margin-top: 5px; }
    &--buy {
        width: 131px;
        height: 40px;
        color: $color-white;
        @include res-font(14, 14);
        font-weight: 500;
        text-transform: uppercase; } }
