.consultation {
    min-height: 680px;
    padding: 100px 0 92px;
    background: url('../images/form-bg.jpg') center (center/cover) no-repeat;
    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }
    &__text {
        color: $color-white;
        @include res-font(20, 15);
        font-weight: medium;
        margin: 80px 0 36px; } }

@media (max-width: 768px) {
    .consultation {
        min-height: 530px;
        padding: 50px 0 40px;
        &__wrapper {
            align-items: center; }
        &__text {
            margin: 30px 0 15px;
            text-align: center; } } }
