.overlay {
    display: none;
    position: fixed;
    background-color: rgba(0, 0, 0, .56);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $bg-modal;
    padding: 38px 40px 40px 40px;
    text-align: center;
    &__close {
        cursor: pointer;
        position: absolute;
        top: -20px;
        right: -40px;
        font-size: 40px;
        color: $main-color; }
    &__title {
        color: $color-titles;
        @include res-font(19, 15);
        font-weight: 700; }
    &__subtitle {
        color: $color-titles;
        @include res-font(14, 12);
        font-weight: 500;
        margin-top: 10px;
        &--width {
            width: 223px;
            max-width: 100%; } } }

#consultation,
#order,
#thanks {
    display: none; }

@media (max-width: 575px) {
    .modal {
        &__close {
            top: -10px;
            right: 10px; } } }
